<template>
    <div class="store-shelf-wrapper">
        <div class="store-shelf">
            <shelf-title
                :title="shelfCategory.title"
                @showDialog="showDialog"
            ></shelf-title>
            <scroll
                class="store-shelf-scroll-wrapper"
                :top="0"
                :bottom="scrollBottom"
                @onScroll="onScroll"
                ref="scroll"
                v-if="ifShowList"
            >
                <shelf-list
                    :top="42"
                    :data="shelfCategory.itemList"
                ></shelf-list>
            </scroll>
            <div class="store-shelf-empty-view" v-else>
                {{ $t('shelf.groupNone') }}
            </div>
            <shelf-footer @showDialog="showDialog"></shelf-footer>
        </div>
        <div class="store-shelf-footer-popup">
            <popup
                :title="shelfFooterPopupProps.title"
                :btn="shelfFooterPopupProps.btn"
                ref="popup"
                @hide="hide"
            ></popup>
        </div>
        <div class="store-shelf-group-dialog">
            <shelf-group-dialog
                ref="shelfGroupDialog"
                :showNewGroup="dialogProps.showNewGroup"
                :groupName="dialogProps.groupName"
            ></shelf-group-dialog>
        </div>
    </div>
</template>

<script>
import shelfTitle from '@/components/shelf/shelfTitle'
import shelfList from '@/components/shelf/shelfList'
import shelfFooter from '@/components/shelf/shelfFooter'
import Scroll from '@/components/common/Scroll'
import { storeShelfMixin } from '../../utils/mixin'
import { realPx } from '../../utils/utils'
// 本应用create-api把组件封装成一个函数
import Popup from '@/components/common/Popup'
import ShelfGroupDialog from '@/components/shelf/ShelfGrouDialog.vue'

export default {
    mixins: [storeShelfMixin],
    components: {
        shelfTitle,
        Scroll,
        shelfList,
        shelfFooter,
        Popup,
        ShelfGroupDialog
    },
    data() {
        return {
            scrollBottom: 0
        }
    },
    computed: {
        ifShowList() {
            return (
                this.shelfCategory.itemList &&
                this.shelfCategory.itemList.length > 0
            )
        }
    },
    methods: {
        onScroll(offsetY) {
            this.setOffsetY(offsetY)
        },
        hide() {
            this.$refs.popup.hide()
            this.setShelfFooterShowVisible(false)
        },
        showDialog() {
            this.$refs.shelfGroupDialog.show()
        }
    },
    mounted() {
        this.getCategoryList(this.$route.query.title)
        this.setCurrentType(2)
    },
    watch: {
        isEditMode(isEditMode) {
            this.scrollBottom = isEditMode ? realPx(48) : 0
            this.$nextTick(() => {
                if (this.$refs.scroll) {
                    this.$refs.scroll.refresh()
                }
            })
        },
        shelfFooterShowVisible(val) {
            if (val) {
                this.$refs.popup.show()
            } else {
                this.$refs.popup.hide()
            }
        }
    }
}
</script>

<style lang="scss" scoped>
@import '@/assets/styles/global';
.store-shelf-wrapper {
    .store-shelf {
        position: relative;
        width: 100%;
        height: 100%;
        background: white;
        z-index: 100;
        .store-shelf-scroll-wrapper {
            position: absolute;
            top: 0;
            left: 0;
            z-index: 101;
        }
        .store-shelf-empty-view {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            padding-top: 50%;
            font-size: px2rem(14);
            color: #333;
            @include center;
        }
    }
    .store-shelf-footer-popup {
        width: 100%;
        height: 100%;
        position: relative;
    }
}
</style>
